<template>
    <div class="handoverduty" v-loading="loading">
        <div class="header">
            <el-button type="text" @click="goback()"> < </el-button>
        </div>
        <div class="outerbox">
                    <div class="innerbox">
                        <el-row class="bordercss">
                            <el-col :span="16">
                                <el-col :span="12"> 2018-10-18 17:47:21</el-col>
                                <el-col :span="12"> </el-col>
                                <el-col :span="24" class="title">
                                    <el-col :span="12">
                                        <h3 class="tlc"></h3>
                                    </el-col>
                                    <el-col :span="12">
                                        <h3 class="tlc"></h3>
                                    </el-col>
                                    <el-col :span="12">
                                        <h3 class="number">{{handOverDutyData.bill_done_pay_num || ''}}</h3>
                                    </el-col>
                                    <el-col :span="12">
                                        <h3 class="number">{{handOverDutyData.bill_real_value}}</h3>
                                    </el-col>
                                    <el-col :span="12">
                                        <h3 class="tlc"></h3>
                                    </el-col>
                                    <el-col :span="12">
                                        <h3 class="tlc"></h3>
                                    </el-col>
                                    <el-col :span="12">
                                        <h3 class="number">{{handOverDutyData.bill_undone_pay_num}}</h3>
                                    </el-col>
                                    <el-col :span="12">
                                        <h3 class="number">{{handOverDutyData.bill_undone_value}}</h3>
                                    </el-col>
                                    <el-col :span="12">
                                        <h3 class="tlc"></h3>
                                    </el-col>
                                    <el-col :span="12">
                                        <h3 class="tlc"></h3>
                                    </el-col>
                                    <el-col :span="12">
                                        <h3 class="number">{{cach_real_render()}}</h3>
                                    </el-col>
                                    <el-col :span="12">
                                        <h3 class="number">{{vip_member_render()}}</h3>
                                    </el-col>
                                    <el-col :span="12"  >
                                        <p class="number" style="font-size:14px;">(:{{handOverDutyData.bill_cash_value}}   :{{handOverDutyData.member_cash_value}})</p>
                                    </el-col>
                                    <el-col :span="12" >
                                        <p class="number" style="font-size:14px;">(:{{handOverDutyData.member_in_cash_value}}   :{{handOverDutyData.member_out_cash_value}})</p>
                                    </el-col>
                                </el-col>
                            </el-col>
                            <el-col :span="8" >
                                <el-col :span="24" style="text-align:center;margin-bottom:10px;">------</el-col>
                                <ul v-if="handOverDutyData.pay_way_sum">
                                    <li v-for="item in handOverDutyData.pay_way_sum" :key="item.pay_name" v-show="item.cross_tag == '1'">
                                        <div class="left_"> {{item.pay_name}}</div>
                                        <div class="right_">{{item.pay_amount}}</div>
                                    </li>
                                </ul>
                                <h5 class="h5css" v-if="!handOverDutyData.pay_way_sum"></h5>
                                <el-col :span="24" style="text-align:center;margin-bottom:10px;">------</el-col>
                                <ul v-if="handOverDutyData.pay_way_sum">
                                    <li v-for="item in handOverDutyData.pay_way_sum" :key="item.pay_name" v-show="item.cross_tag == '2'">
                                        <div class="left_"> {{item.pay_name}}</div>
                                        <div class="right_">{{item.pay_amount}}</div>
                                    </li>
                                </ul>
                                <h5 class="h5css" v-if="!handOverDutyData.pay_way_sum"></h5>
                            </el-col>
                        </el-row>
                        <el-row style="padding-top:30px;">
                            <el-col :span="16">
                                 <el-checkbox v-model="PrintHandoverCheackbox"></el-checkbox>
                                 <el-checkbox v-model="PrintPrintDishesCheackbox"></el-checkbox>
                            </el-col>
                            <el-col :span="8" style="    text-align: right;padding-right: 40px;">
                                 <el-button type="primary" style="width: 200px;" @click="setCross_()"></el-button>
                            </el-col>
                        </el-row>
                    </div>
                </div>
    </div>
</template>

<script >
import Vue from "vue";
import { mapState, mapMutations, mapActions } from "vuex";
import dappSDK from "dappsdk";
import {getCashierCross,setCross} from '../../../../service/handOverDuty/index.js'

export default {
    name: "index",
    data() {
        return {
            activetab: "handoverduty",
            handOverDutyData:null,
            loading:false,
            PrintHandoverCheackbox:false,
            PrintPrintDishesCheackbox:false,

        };
    },
    components: {},
    computed: {
        /*catch store.data*/
        ...mapState({
            token_: state => state.token_
        })
    },
    mounted() {
        window.rePage = this.rePage;
        // this.rePage('order')
        //token
        // this.Initialization_();
        this.getCashierCross_();

       
    },
    methods: {
        ...mapMutations(["settoken"]),

        /**
         * 1
         * @param 1 url
         * @returns  void
         */
        handleClick(tab, event) {
            console.log(tab, event);
        },

        goback() {
             this.$router.push({
              path: "/handOverDuty"
          });
        },

        /**
         * 
         */
         async getCashierCross_(){
             this.loading=true;
            let param={
                token:this.token_
            };  
            let {code,data,message} = await getCashierCross(param);
            if(code == 200){
                this.handOverDutyData = data;
            }
            this.loading=false;
        },

        print_(){
            const that = this;
            let param ={
                PrintHandover:that.PrintHandoverCheackbox,//
                PrintDishes:that.PrintPrintDishesCheackbox,//
                HandoverData:that.handOverDutyData,//data
            };
            dappSDK.exec("system", "handoverPrint", param, function(err, data) {
                if (err) {
                    var code = error.code;
                    if (code == "-10001") {
                        console.log(
                            "namespace, sdknative, appsdk"
                        );
                    }
                    if (code == "-10002") {
                        console.log(
                            "function, sdknative, appsdk"
                        );
                    }
                    if (code == "-10003") {
                        console.log("params");
                    }
                    if (code == "-10004") {
                        console.log("params");
                    }
                    console.log(code);
                    // , return
                    return alert("Error: " + err.message);
                }
            });
        },

         /**
         * 
         */
        async setCross_(){
            this.loading=true;
             let param={
                token:this.token_
            };  
            let {code,data,error} = await setCross(param);
            if(code == 200){
                this.$message({
                        message: data,
                        type: 'success'
                });
                this.print_();
                this.goback();
            }else{
                this.$message({
                    message: error.msg,
                    type: 'error'
                });
            }
            this.loading=false;
        },
        
        /**
         *  =  + 
         */
        cach_real_render(){
            return (Number(this.handOverDutyData.member_cash_value) + Number(this.handOverDutyData.bill_cash_value)).toFixed(2);
        },
        /**
         *  =  - 
         */
        vip_member_render(){
            return (Number(this.handOverDutyData.member_in_cash_value) - Number(this.handOverDutyData.member_out_cash_value)).toFixed(2);
        },



    }
};
</script>
<style>
body,
html {
    height: 100%;
}
.el-tabs__nav-wrap is-top {
    height: 64px;
}
.el-tabs__nav-scroll {
    height: 64px;
    padding-left: 40px;
}
.el-tabs__nav {
    line-height: 64px !important;
}
.el-tabs__header {
    margin: 0 !important;
}
</style>
<style lang = "less" scoped>
@import "style.less";
</style>



